<template>
  <v-app style="background-color: rgb(247, 247, 247);" id="demo">
    <Header></Header>
    <div>
      <div style="text-align:center;font-size:30px;margin-top:30px;font-family:'bgtitlefamily';"> {{ title }} </div>
      <v-card v-if="minutes!=0"
      style="text-align:center;font-size:20px;width:10vw;margin-left:45%;margin-top:10px;font-family:'bgtitlefamily';">
        <h1 v-if="minutes>=10&&seconds<10">{{minutes}}:0{{seconds}}</h1>
        <h1 v-else-if="minutes<10&&seconds>=10">0{{minutes}}:{{seconds}}</h1>
        <h1 v-else-if="minutes<10&&seconds<10">0{{minutes}}:0{{seconds}}</h1>
        <h1 v-else>{{minutes}}:{{seconds}}</h1></v-card>
      <div style="margin: 20px 200px;">
        <v-card>
          <v-card-text style="text-align:left;
       font-size:20px;margin-top:20px;padding-top:20px;font-family:'bgtitlefamily';">{{paperDescription}}</v-card-text>
          <div v-for="(item, index) in question" :key="index" style="margin:5px 40px;">
            <div v-if="index == 0" style="height:20px;"></div>
            <!-- 题目标号 -->

            <div style="font-size:20px;position:relative;top:10px;">
              <span style="color:red;" v-if="item.necessary==true || item.necessary=='true'">* </span>
              <span v-if="showSeq==true||showSeq=='true'">{{ (page-1)*4+index + 1 + '. ' }}</span> {{ item.title }}
            </div>

            <p style="margin-top:1vh;margin-left:1.5vw;">{{ item.description}}</p>
            <!-- 多选题、多选投票题、多选报名题 -->
            <div v-if="item.type == '多选题' || item.type == '多选投票题' || item.type == '多选报名题'">
              <v-container>
              <div v-for="(subItem, i) in item.choices"
                    :key="i">
                <v-checkbox v-model="item.Answer" :label="subItem" hide-details
                :value="subItem"
                >
                <template v-slot:label>
                  <div  style="width:30vw;">
                    {{subItem}}
                  </div>
                </template>
                </v-checkbox>
                <p v-if="item.ptype==1" style="float:right;margin-top:-2.7vh;margin-right:10px;color:black;" >0票</p>
                <p v-else-if="item.ptype==0" style="float:right;margin-top:-5vh;margin-right:10px;color:black;" >已报名0人(剩余{{item.numLimit[i]}}人)</p>
              </div>
            </v-container>
            </div>
            <!-- 单选题、单选投票题、单选报名题 -->
            <div v-if="(item.type == '单选题'&& item.title !=='您的体温是:')||item.type == '单选投票题'||item.type == '单选报名题'" >
              <v-container>
                <v-radio-group v-model="item.answer">
                  <div v-for="(subItem, i) in item.choices" :key="i">
                    <v-radio :value="subItem" style="height:40px;">
                      <template v-slot:label>
                        <div  style="width:30vw;">
                          {{ subItem }}
                        </div>
                      </template>
                    </v-radio>
                    <span v-if="item.ptype==1" style="float:right;margin-top:-3.2vh;margin-right:10px;color:black;" >0票</span>
                    <span v-else-if="item.ptype==0" style="float:right;margin-top:-5vh;margin-right:10px;color:black;" >已报名0人(剩余{{item.numLimit[i]}}人)</span>
                  </div>
                </v-radio-group>
              </v-container>
            </div>
            <div v-if="item.type === '单选题'&& item.title ==='您的体温是:'">
              <div style="display:inline-block;width:400px;margin-left:20px;margin-top:20px;">
                <v-container fluid>
                  <v-select
                      v-model="tp"
                      :items="item.choices"
                      menu-props="auto"
                      label="Select"
                      hide-details
                      prepend-icon="mdi-map"
                      single-line
                  ></v-select>
                </v-container>
              </div>
            </div>
            <!-- 填空题 -->
            <div v-if="item.type == '填空题'">
              <div style="display:inline-block;width:400px;margin-left:20px;margin-top:20px;">
                <v-text-field
                    v-model="item.answer"
                    label="请输入你的答案"
                    clearable
                ></v-text-field>
              </div>
            </div>
            <!-- 打分题 -->
            <div v-if="item.type == '打分题'">
              <div style="display:inline-block;width:400px;margin-left:20px;margin-top:20px;">
                <v-rating
                  v-model="item.answer" style="margin-left:2vw;"
                  :length="item.limit"
                  color="yellow lighten-1"
                  background-color="grey lighten-1"
                  mid
              ></v-rating>
              </div>
            </div>
            <!-- 文本题 -->
            <div v-if="item.type == '文本题'">
              <div style="display:inline-block;width:400px;margin-left:20px;margin-top:20px;">
                <v-textarea style="width:30vw;margin-left:0vw;margin-top:1vh;" dense
                    v-model="item.answer"
                    label="请输入你的答案"
                    clearable
                ></v-textarea>
              </div>
            </div>
            <!-- 下拉框 -->
            <div v-if="item.type == '下拉框'">
              <div style="display:inline-block;width:400px;margin-left:20px;margin-top:20px;">
                <v-select
                    v-model="item.answer"
                    :items="item.choices"
                    item-text="option"
                    item-value="abbr"
                    label="请选择你的选择"
                    persistent-hint
                    return-object
                    single-line
                ></v-select>
              </div>
            </div>
            <div v-if="item.type == '定位题'">
              <div style="display:inline-block;width:400px;margin-left:20px;margin-top:20px;">
                <FillLocation v-model="question[index]" v-if="item.type==='定位题'"></FillLocation>
              </div>
            </div>
            <!-- 时间选择器 -->
            <div v-if="item.type == '时间选择器'">
              <div style="display:inline-block;width:400px;margin-left:20px;margin-top:20px;">
              <v-menu
                  v-model="item.menu"
                  :close-on-content-click="false"
                  offset-y
                  min-width="auto"
                >
                  <template v-slot:activator="{ on, attrs }">
                    <v-text-field
                      append-icon="mdi-calendar"
                      readonly
                      v-bind="attrs"
                      v-on="on"
                      outlined
                      dense v-model="item.date"
                      background-color="white"
                      style="width: 12vw; margin-left: 0"
                    ></v-text-field>
                  </template>
                  <v-date-picker
                v-model="item.date" @input="item.menu = false"></v-date-picker>
                </v-menu>
                <v-menu
                  ref="menu"
                  v-model="item.menu1"
                  :close-on-content-click="false"
                  :nudge-right="40"
                  :return-value.sync="time"
                  offset-y
                  max-width="290px"
                  min-width="290px"
                >
                  <template v-slot:activator="{ on, attrs }">
                    <v-text-field
                      v-model="item.time"
                      append-icon="mdi-clock-time-four-outline"
                      readonly
                      v-bind="attrs"
                      v-on="on"
                      outlined
                      background-color="white"
                      dense
                      style="width: 12vw;float:right;margin-top:-9vh;"
                    ></v-text-field>
                  </template>
                  <v-time-picker
                    v-model="item.time"
                    full-width
                    @click:minute="item.menu1 = false"
                  ></v-time-picker>
                </v-menu>
              </div>
            </div>
          </div>
          <!-- 翻页 -->
          <div style="text-align:center;">
            <v-btn color="warning" @click="lastPage"
                  style="width:100px;margin:30px 2vw 2vh 2vw;color:white;">上一页</v-btn>
            <v-btn @click="back"
                  style="background-color:skyblue;width:100px;margin:30px 2vw 2vh 2vw;color:white;">返&nbsp;&nbsp;回</v-btn>
            <v-btn color="warning" @click="nextPage"
                  style="background-color:skyblue;width:100px;margin:30px 2vw 2vh 2vw;color:white;">下一页</v-btn>
          </div>
        </v-card>
      </div>
    </div>
  </v-app>
</template>

<script>
import "@/assets/font/font.css";
import htmlToPdf from '@/plugins/htmlToPdf';
import Header from '@/components/Header';
import FillLocation from "@/components/FillBlank/FillLocation";
export default {
  name: 'Preview',
  components: {
      Header,FillLocation
  },
  data() {
      return {
          id: '',
          title: '这是一个调查',
          page:1,
          minutes:0,
          timer:'',
          seconds:0,
          paperDescription:'这是一个调查的问卷简的问卷是一个调查的问卷简介',
          return: '',
          menu:'',
          id:-1,
          totalPage:1,
          questions:[],
          endTime: "",
          showSeq: true,
          isOneTime: 'true',
          time: "",
          date: '',
          question: [
          ],
      }
  },
  computed:{
    getIsShow(){
      if(this.showSeq === 'false'){
        return false;
      }
      else{
        return true;
      }
    }
  },
  mounted(){
    this.showSeq = this.$route.query.showSeq;
    this.return = this.$route.query.return
    if(this.$route.query.minutes!=undefined&&this.$route.query.minutes!='')this.minutes=this.$route.query.minutes
    this.questions=JSON.parse(this.$route.query.question)
    this.title=this.$route.query.title
    this.paperDescription=this.$route.query.paperDescription
    this.id=this.$route.query.id
    this.endTime = this.$route.query.endtime;
    if(this.$route.query.minutes!=undefined)this.minutes=this.$route.query.minutes
    this.question=[]
    for(var i=0;i<this.questions.length;i++) this.questions[i].Answer=[]
    if(this.questions.length%4==0) this.totalPage=this.questions.length/4
    else if(this.questions.length%4!=0) this.totalPage=parseInt(this.questions.length/4)+1
    for(var i=0;i<4&&i<this.questions.length;i++)  this.question.push(this.questions[i])
    // console.log(this.$route.query.title,JSON.parse(this.$route.query.question));
    if(this.$route.query.download == 1) {
      // this.handleDown();
      setTimeout(() => {
        this.$router.replace('/manage');
        this.handleDown();
      }, 1);

    }
    if(this.$route.query.isonetime != undefined){
      this.isOneTime = this.$route.query.isonetime;
    }

    if(this.$route.query.date!=undefined){
      this.date = this.$route.query.date;
    }
    if(this.$route.query.time!=undefined){
      this.time = this.$route.query.time;
    }

  },
  methods: {
    check(item) {
      console.log(item);
    },
    setClock(){
        if(this.seconds>0){
          this.seconds--
        }
        else if(this.minutes==0&&this.seconds==0){
            clearInterval(this.timer);
            this.$message.warning('考试时间到，试卷自动提交')
        }
        else if(this.seconds==0){
          this.seconds=59
          if(this.minutes!=0)this.minutes--
        }
    },
    back(){
      if(this.return == 'manage') {
        this.$router.replace('/manage');
      } else {
        this.$router.replace({path:this.$route.query.url,query:{paperDescription:this.paperDescription,type:0,minutes:this.minutes,title:this.title,question:JSON.stringify(this.questions),showseq:this.showSeq, isonetime:this.isOneTime, time:this.time, date:this.date}})
      }
    },
    lastPage(){
        if(this.page==1) {
            this.$message.warning('这已经是第一页了')
        }
        else{
            this.question=[]
            this.page--
            for(var i=(this.page-1)*4;i<this.page*4&&i<this.questions.length;i++){
                this.question.push(this.questions[i])
            }
        }
    },
    nextPage(){
      if(this.page==this.totalPage) {
          this.$message.warning('这已经是最后一页了')
      }
      else{
          this.question=[]
          this.page++
          for(var i=(this.page-1)*4;i<this.page*4&&i<this.questions.length;i++){
              this.question.push(this.questions[i])
          }
      }
    },
    handleDown(){
      htmlToPdf.downloadPDF( document.querySelector('#demo'),'我的PDF');
    },
  }
}
</script>